.popular {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  max-width: 1280px;
  margin: auto;
  padding: 30px 0;
}
.popular h1 {
  color: #171717;
  font-size: 40px;
  font-weight: 600;
}
.popular hr {
  width: 150px;
  height: 4px;
  border-radius: 10px;
  background: #252525;
}
.popular-item {
  margin-top: 40px;
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  gap: 25px;
  justify-content: center;
}
/* ---------- media query------------ */

@media (max-width: 800px) {
  .popular h1 {
    font-size: 25px;
  }
  .popular hr {
    width: 80px;
    height: 4px;
  }
}
